import { createStore } from 'vuex'

export default createStore({
  state: {
    createData: ''
  },
  mutations: {
    // ËøõË°åÊï∞ÊçÆÊõ¥Êñ∞ÔºåÊîπÂèòÊï∞ÊçÆÁä∂ÊÄÅ
    countType(state, action){
      state.count =  state.count + action.payload
    }
  },
  actions: {
    // ÊâßË°åÂä®‰ΩúÔºåÊï∞ÊçÆËØ∑Ê±Ç
    addCount({ commit }){
      fetch('../data.json')
        .then(function(response) {
          return response.json();
        })
        .then(function(myJson) {
          let action = {
            type: 'countType',
            payload: myJson.text
          }
          commit(action)
        });
    }
  },
  getters: {
    // Ëé∑ÂèñÂà∞ÊúÄÁªàÁöÑÊï∞ÊçÆÁªìÊûú
    getCount(state){
      return state.count
    }
  }
})