<template>
  <div ref="telescopicAssembly" class="telescopicAssembly">
    <el-tooltip :disabled="!isShow" :content="textData" :placement="placement" :open-delay="openDelay">
      <div ref="assemblyText" class="assemblyText" @mouseover="mouseover" @mouseout="mouseout">{{ textData }}</div>
    </el-tooltip>
  </div>
</template>

<script>
// ÊñáÊú¨ÊèêÁ§∫Ê°Ü,ÁõÆÂâçÈªòËÆ§Âè™ÊúâÊòæÁ§∫ÁúÅÁï•Âè∑ÁöÑÊâçÊòæÁ§∫
export default {
  name: '',
  filters: {
    ellipsis(value) {
      return false
    }
  },
  props: {
    // ÊòæÁ§∫ÁöÑÊï∞ÊçÆ
    textData: { type: String, default: '' },
    // ËÆæÁΩÆÊúÄÂ∞èÂÆΩÂ∫¶
    minWidth: { type: Number, default: 30 },
    // ËÆæÁΩÆÊúÄÂ§ßÂÆΩÂ∫¶,Ë∂ÖÂá∫ÈÉ®ÂàÜÊòæÁ§∫ÁúÅÁï•Âè∑ 0‰∏çÁîüÊïà
    maxWidth: { type: Number, default: 0 },
    // ËÆæÁΩÆlinÁöÑÈ´òÂ∫¶ 0‰∏çÁîüÊïà
    lineHeight: { type: Number, default: 0 },
    // Âà§ÂÆöÊ≤°ÊúâÊ∫¢Âá∫ÁöÑÊñáÂ≠óÊòØÂê¶ÊòæÁ§∫,ÈªòËÆ§‰∏çÊòæÁ§∫
    noOverflowShow: { type: Boolean, default: false },
    // ÈªòËÆ§‰∏∫ top È°∂ÈÉ®ÊòæÁ§∫ ÂÖ∑‰ΩìÊñπ‰ΩçÁúã el-tooltip ÁªÑ‰ª∂
    placement: { type: String, default: 'top' },
    openDelay: { type: Number, default: 0 }
  },
  data() {
    return {
      isShow: false // Áî®Êù•Âà§ÂÆöÊòØÂê¶ÊòæÁ§∫ÊèêÁ§∫Ê°Ü
    }
  },
  watch: {
  },
  created() {
    this.initialization()
  },
  methods: {
    async initialization() {
      await this.$nextTick
      if (this.$props.minWidth) { this.$refs.assemblyText.style.minWidth = `${this.$props.minWidth}px` }
      if (this.$props.maxWidth) { this.$refs.assemblyText.style.maxWidth = `${this.$props.maxWidth}px` }
      if (this.$props.lineHeight) { this.$refs.assemblyText.style.lineHeight = `${this.$props.lineHeight}px` }
    },
    // Èº†Ê†áÁßªÂÖ•ÁöÑÊó∂ÂÄô
    mouseover(evt) {
      if (this.$props.noOverflowShow || this.$refs.assemblyText.scrollWidth > this.$refs.assemblyText.clientWidth) {
        this.isShow = true
      }
    },
    // Èº†Ê†áÁßªÂá∫ÁöÑÊó∂ÂÄô
    mouseout() {
      this.isShow = false
    }
  }
}
</script>

<style scoped lang="scss">
.telescopicAssembly{
  position: relative;
  .suspension{
    white-space: nowrap;
    background-color: #303133;
    position: absolute;
    color: white;
    top:-25px;
    left: -2px;
    z-index: 10;
    padding: 4px;
    border-radius: 5px;
    font-size: 12px;
  }
  .assemblyText{
    text-overflow: ellipsis;
    overflow: hidden;
    white-space:nowrap
  }
}
</style>
